<template>
  <div class="wrap wide">
    <div class="banner wide">
      <img class="wide" src="@/assets/images/medical.png" alt="">
    </div>
    <div class="cov-tab epidemic_content">
      <van-tabs v-model="active" animated @click="onActive">
        <van-tab v-for="(tab ,index) in hospitalData.tabInfo" :title="tab.title">
          <div class="lv_search" @click="cityQuery">
            <div class="lv_search_info">
              <div class="iconfont icon-jiantou_zuoyouqiehuan">切换</div>
              <div v-if="cityData.length !==0">{{cityData[values[0]].name}}</div>
              <div v-if="active !== 2 && cityData.length !==0">{{cityData[values[0]].children[values[1]].name}}</div>
              <div class="iconfont icon-xiala2"></div>
            </div>
          </div>


          <div class="epidemic_list" v-show="active===0">
            <div v-for="(item,index) in hospListArr.designatedHospital" class="epidemic_list_row line1" :key="index">
              <span>{{item.name}}</span>
            </div>
          </div>
          <div class="epidemic_list" v-show="active===1">
            <div v-for="(item,index) in hospListArr.feverClinic" class="epidemic_list_row line1" :key="index">
              <span>{{item.name}}</span>
            </div>
          </div>
          
            <div class="epidemic_list" v-show="active===2">
            <div v-for="(item,index) in MaternityArr.pregnantWoman" class="epidemic_list_row line1" :key="index">
              <span>{{item.name}}</span>
            </div>
          </div>

        </van-tab>
      </van-tabs>
    </div>

    <van-popup round v-model="show" position="bottom">
      <van-picker toolbar-position="top" show-toolbar @cancel="show = false" title="切换城市" :columns="columns"
        @confirm="onConfirm" />
    </van-popup>
  </div>
</template>

<script>
import { hospitalQuery, province_city, hospitalList ,Maternityhospital} from '@/api/indexCovid'
export default {
  data() {
    return {
      active: 0,
      hospitalData: [],
      cityData: [],
      show: false,
      columns: [],
      hospListArr: [],
      values:[0,0],
      MaternityArr:[],
      cityNum:'',
      cityN:'',
    }
  },
  mounted() {
    this.hospital();
    this.city();
    this.hospList('')
  },
  // watch:{
  //   active:{
  //     handler(newVal,oldNew){
  //       let cityVal = [];
  //       if(newVal !== 2){
  //         cityVal.push([
  //           this.cityData[this.values[0]].text,
  //           this.cityData[this.values[0]].children[this.values[1]].text
  //         ])
  //       }else{

  //       }
  //       console.log(cityVal);
  //     }
  //   }
  // },
  methods: {
    async hospital() {
      let hospitalData = await hospitalQuery();
      this.hospitalData = hospitalData.data;
    },
    async city() {
      this.columns = [];
      let cityData = await province_city();
      cityData.data.forEach(item => {
        item.text = item.name;
        item.items.forEach(city => city.text = city.fname);
        item.children = item.items
      });
      this.cityData = cityData.data;
      if(this.active!==2){
        this.columns = cityData.data
      }else{
        cityData.data.forEach(item=>{
          this.columns.push({
            text:item.name,
            code:item.code
          });
        })
      }
      
    },
    cityQuery() {
      this.show = true
    },
    onConfirm(values, arr) {
      this.show = false
      if(this.active !== 2){
        let cityNumber = this.columns[arr[0]].children[arr[1]]
        this.hospList(cityNumber.code);
        this.values = arr;
        this.cityNum = this.columns[arr[0]].code;
      }else{
        let cityNumber = this.columns[arr];
        this.Maternity(cityNumber.code);
        this.values = [arr,0];
        this.cityN = this.cityData[arr].children[0].code;
        this.cityNum = this.columns[arr].code;
      }
      
    },
    async hospList(code) {
      let hospListData = await hospitalList(code);
      this.hospListArr = hospListData.data;
    },

    async Maternity(code) {
      let MaternityData = await Maternityhospital(code);
      this.MaternityArr = MaternityData.data;
    },
    onActive() {
      this.city();
      this.Maternity(this.cityNum || "CN11000000000000");
      this.hospList(this.cityN)
    }
  },
}
</script>
<style lang="less" scoped>
/deep/ .van-tab--active {
  font-weight: bold;
}

/deep/ .van-tab {
  font-size: 36px;
}

/deep/ .van-tab__text {
  height: 100%;
  line-height: 110px;
  color: #111;
}

/deep/ .van-tabs--line .van-tabs__wrap {
  height: 110px;
}

.wrap {
  height:1334px;
  background-color: #fff;
  .epidemic_content {
    background: #fff;
    margin-top: -30px;
    border-radius: 30px 30px 0 0;
    z-index: 1;
    overflow: hidden;

    .lv_search {
      position: relative;

      .lv_search_info {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 100px;

        font-size: 30px;
        box-shadow: 0 10px 20px 0 rgb(0 0 0 / 10%)
      }
    }

    .epidemic_list {
      padding: 30px;
    }

    .epidemic_list_row {
      height: 100px;
      background: #f8f8f8;
      border-radius: 10px;
      padding: 0 40px;
      margin-bottom: 20px;
      line-height: 100px;
      font-size: 30px;
      color: #000;

    }
  }
}
</style>